.flight-filter {
   font-family: Livvic-Regular;
   width: 100%;

   .dropdown-container {
      display: flex;
      justify-content: center;
      flex-wrap: wrap;
      gap: 10px;
      margin-top: 30px;
   }

   label {
      margin-top: 5px;
      font-size: rem(14px);
      color: #FFF;
      // text-shadow: 0px 0px 5px black;
      border-radius: 4px 4px 0px 0px;
      background-color: #121212bc;

      .select {
         width: 124px;
         color: #121212;
      }
      .select-larger {
         width: 130px;
         color: #121212;
      }
   }

}