.flight-dashboard {
   display: flex;
   justify-content: center;
   align-items: center;
   font-family: Livvic-Regular;
   margin-top: 10px;
   flex-direction: column;

   h1 {
      margin-top: 0px;
      margin-bottom: 10px;
   }

   .box-container {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 10px;

      .box-1 {
         width: 28%;
         height: 20%;
         padding: 8px;
         text-align: center;
         border-radius: 10px;
         min-width: 120px;
         min-height: 140px;
         display: flex;
         align-items: center;
         flex-direction: column;
         max-height: 140px;
         justify-content: space-around;

         h4 {
            margin-top: 0px;
            margin-bottom: 0px;
         }

         .box-1-data {
            height: 60%;
            display: flex;
            flex-grow: 1;
            align-items: center;
            justify-content: center;
            font-size: rem(40px);

            span {
               margin-top: 14px;
               font-size: rem(16px);
            }
         }

         .box-1-sub-data {
            display: flex;
            align-items: center;
            gap: 5px;
         }
      }
   }

}